body {
      background-color: rgb(245, 230, 230);
      font-family: 'Potta One', cursive;
font-family: 'Staatliches', cursive;
      text-align: center;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      min-height: 100vh;
  }

  
  .app-container {
    height: 400px;
    width: 300px;
    background: #2b5870;
    border-radius: 20px;
    box-shadow: 30px 30px 38px 0px rgba(0, 0, 0, 0.75);
  }
  
  .temperature-display-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 70%;
    font-size: 179px;
  }
  
  .temperature-display {
    display: flex;
    border-radius: 50%;
    color: rgb(245, 230, 230);
    height: 220px;
    width: 220px;
    text-align: center;
    justify-content: center;
    align-items: center;
    font-size: 48px;
    /* border: 3px #ffffff solid; */
    transition: background 0.5s;
  }
  
  button {
    border-radius: 100px;
    height: 80px;
    width: 80px;
    font-size: 32px;
    background: #2b5870;
    color: rgb(245, 230, 230);
    border: 2px #ffffff solid;
  }
  
  button:hover {
    background: rgb(184, 184, 184);
    cursor: pointer;
  }
  
  button:focus {
    outline: 0;
  }
  
  .button-container {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
  }
  
  .neutral {
    background: rgb(184, 184, 184);
  }
  
  .cold {
    background: #035aa6;
  }
  
  .hot {
    background: #ff5200;
  }
  